<template>
    <div class="items-list">
        <ul class="items" v-if="data.items.length">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.code }"
                @click="emit('change', item)"
            >
                <span class="artist-preview">
                    <Img v-if="item.profileImage?.resized || defaultProfileImage?.resized"
                        :preview="item.profileImage?.resized || defaultProfileImage?.resized"
                        :original="item.profileImage?.original || defaultProfileImage?.original"
                        :alt="item?.name"
                        :skeleton="true"
                    />
                    <span>
                        {{ item.name }}
                    </span>

                    <Icon icon="chevron-right" />
                </span>
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t(`blocks.artists.load`)"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            animate-icon="artists"
            class="empty"
            :title="props.empty?.title"
            :description="props.empty?.description"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import { delay } from 'ðŸ”—/utils/utils';
    import Img from 'ðŸ”—/components/ui/Img.vue';

    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        empty: { type: Object, default: () => ({}) },
        sort: { type: Object, default: () => ({
            code: 'name',
            direction: 'asc'
        }) },
        source: { type: Object, default: () => ({ store: 'artists', fnName: 'list' }) }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 30, // Temp
        total: 0,
        sort: props.sort
    });

    const defaultProfileImage = computed(() => {
        return api.artists().defaultProfileImage;
    });

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params = {
            skip: data.skip,
            take: data.take
        };

        // if (data.sort) {
        //     params.sort = data.sort.code;
        //     params.sort_dir = data.sort.direction;
        // }

        const payload = await api[props.source.store]()[props.source.fnName](params) || [];

        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    defineExpose({
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include default-items-list;

    .items-list {
        .items {
            li {
                @include artist-list-item;
            }
        }
    }
</style>