    import { useI18n } from 'vue-i18n';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import Artists from 'ðŸ”—/components/blocks/Artists.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';

    const { t } = useI18n();

    import stores from '@/stores';

    const tg = stores.tg();

    const data = reactive({
        ready: false
    });

    const init = async () => {
        data.ready = true;
    }

    onMounted(() => {
        tg.showBackButton();
    });

    onBeforeUnmount(() => {
        tg.hideBackButton();
    });
</script>

<style lang="scss" scoped>
    .head {
